<template>
    <div>
        <div class="kd-page-title">
            {{ getTitle() }}
        </div>
        <div
            class="kd-page-content-wrapper wrap-info"
            v-if="step === 0"
        >
            <el-form
                class="uploadLabel"
                label-width="15vw"
            >
                <el-form-item label="材料上传：">
                    <div class="imgListsWrap">
                        <div
                            class="uploadImg"
                            v-for="(item,index) in photoList"
                            :key="index"
                        >
                            <img
                                class="preImg"
                                :src="item"
                                :key="index"
                            >
                            <i
                                @click="removeImg(index)"
                                class="delImg"
                            />
                        </div>
                        <div
                            v-if="!(photoList.length > 0)"
                            class="uploadButton"
                            @click="goToPhoto"
                        >
                            <p :class="isLoad ? 'kedaIconfont kedaIcon-zhaoxiangji' : 'el-icon-loading'"/>
                        </div>
                    </div>
                </el-form-item>
            </el-form>
            <div class="footer">
                温馨提示：请上传延期原因的相关证明材料
            </div>
        </div>
        <div
            class="kd-page-content-wrapper wrap-img"
            v-if="step === 1"
        >
            <img
                :src="highPhotoImg"
                alt=""
                width="400"
                height="300"
            >
        </div>
        <div
            class="kd-page-content-wrapper wrap-img"
            v-if="step === 2"
        >
            <img
                :src="nowImg"
                alt=""
                width="400"
                height="300"
            >
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                v-if="step === 0"
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                v-if="step === 0"
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                v-if="step === 0"
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
                :but-disable="photoList.length === 0"
            />

            <kd-button
                v-if="step === 1"
                icon="kedaIcon-zhaoxiangji"
                text="拍照"
                :button-handle="startScan"
            />
            <kd-button
                v-if="step === 1"
                icon="kedaIcon-chexiao1"
                text="返回"
                :button-handle="goToIndex"
            />

            <kd-button
                v-if="step === 2"
                icon="kedaIcon-baocun"
                text="保存"
                :button-handle="save"
            />
            <kd-button
                v-if="step === 2"
                icon="kedaIcon-zhaoxiangji"
                text="重拍"
                :button-handle="goToPhoto"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { wsSend } from '@/utils/websocket';
import { saveProveData } from '@/service/traffic/driver/delayExchange';
import { cancelPrefix } from '../../../../../utils/tools';

export default {
    name: 'DelayExchangeEvidenceReason',
    data () {
        return {
            step: 0,
            nowImg: '',
            isLoad: false,
            photoList: []
        };
    },
    computed: {
        ...mapState('permanent', {
            dialog: 'highPhotoDialog',
            lightWebSocket: 'lightWebSocket',
            highPhotoDev: 'highPhotoDev',
            highPhotoImg: 'highPhotoImg'
        }),
        ...mapState('delayExchange', {
            materialPhoto: 'materialPhoto'
        })
    },
    mounted () {
        if (this.materialPhoto.length > 0) {
            this.isLoad = false;
            this.photoList = this.materialPhoto;
        } else {
            setTimeout(() => {
                this.isLoad = true;
            }, 3000);
        }
        this.dialog.html_loaded('one');
    },
    beforeDestroy () {
        // 关闭高拍仪主视频
        this.dialog.get_actionType('closeSignal');
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('delayExchange', ['setMaterialPhoto']),
        getTitle () {
            if (this.step === 0) {
                return '上传证明材料';
            } else if (this.step === 1) {
                return '请将证明放置于高拍仪区域内';
            } else if (this.step === 2) {
                return '上传证明材料';
            }
        },
        // 进入第一步
        async goToIndex () {
            this.step = 0;
            wsSend(this.lightWebSocket, 'lightCtrl@B6:OFF');
        },
        // 进入第二步
        goToPhoto () {
            if (this.isLoad) {
                this.step = 1;
                wsSend(this.lightWebSocket, 'lightCtrl@B6:ON');
            }
        },
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            const res = await saveProveData({
                tp: cancelPrefix(this.photoList[0])
            });
            if (res && res.code === '0') {
                this.setMaterialPhoto([...this.photoList]);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        },
        // 拍照，进入第三部
        startScan () {
            this.nowImg = this.highPhotoImg;
            this.step = 2;
        },
        // 保存数据，并返回第一步
        save () {
            this.step = 0;
            this.photoList = [this.nowImg];
        },
        removeImg (index) {
            this.photoList.splice(index, 1);
        }

    }
};
</script>

<style lang="scss" scoped>
    .bg {
        width: 34.37vw;
        height: 26.56vh;
        border-radius: 0.5vh;
        background: url("../../../../../assets/images/cardno_bg.png");
        background-size: 100% 100%;
        margin: 0 auto;
    }

    .wrap-img {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 400px;
            height: 300px;
            background: #fff;
        }
    }

    .wrap-info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 5vw 0;

        ::v-deep .el-form-item__content {
            margin-right: 10vw;
        }

        .imgListsWrap {
            display: flex;

            .uploadImg {
                margin-right: 20px;
                border-radius: 6px;
                border: 1PX solid rgba(242, 249, 255, 1);/*no*/
                width: 13vw;
                height: 13vw;
                position: relative;

                .preImg {
                    width: 100%;
                    height: 100%;
                    border-radius: 6px;
                }

                .delImg {
                    position: absolute;
                    top: -15px;
                    right: -15px;
                    z-index: 3;
                    width: 30px;
                    height: 30px;
                    background: url("../../../../../assets/images/del.png");
                    background-size: 100% 100%;
                }
            }

        }

        .uploadButton {
            background: rgba(255, 255, 255, 0.8);
            border-radius: 6px;
            border: 1PX solid rgba(242, 249, 255, 1);/*no*/
            width: 13vw;
            height: 13vw;
            text-align: center;

            p {
                font-size: 5vh;
                color: #85BAF0;
                text-align: center;
                line-height: 13vw;
            }
        }

        .footer {
            font-size: 22px;
            font-weight: 400;
            line-height: 40px;
            margin-left: 3vw;
            width: 60vw;
        }
    }
</style>
