<template>
    <div>
        <server-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <confirm-info v-if="currentStep === steps.findIndex(step=>step.key === 'confirmInfo')"/>
        <reason v-if="currentStep === steps.findIndex(step=>step.key === 'reason')"/>
        <upload v-if="currentStep === steps.findIndex(step=>step.key === 'upload')"/>
        <input-address v-if="currentStep === steps.findIndex(step=>step.key === 'inputAddress')"/>
        <check-phone v-if="currentStep === steps.findIndex(step=>step.key === 'checkPhone')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'completeProcess')"
            :handle-business="handleComplete"
        />
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="completeInfo"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import process from '@/components/process';
import serverInfo from '@/components/server-info';
import inputAddress from './components/address';
import confirmInfo from './components/confirm-info';
import checkPhone from './components/check-phone';
import reason from './components/reason';
import upload from './components/upload';
import complete from '@/components/complete';
import { completeBusiness } from '@/service/traffic/driver/delayExchange';

export default {
    name: 'DelayExchangeEvidenceIndex', // 延期换证
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        process,
        serverInfo,
        confirmInfo,
        checkPhone,
        inputAddress,
        complete,
        reason,
        upload
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            businessTypeCode: 'businessTypeCode'
        }),
        ...mapState('delayExchange', {
            completeInfo: 'completeInfo'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setSteps']),
        ...mapMutations('delayExchange', ['setBusinessInfo', 'setCompleteInfo']),
        async handleComplete () {
            const res = await completeBusiness({});
            if (res && res.code === '0') {
                this.setCompleteInfo(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    },
    mounted () {
        this.setSteps([
            {
                name: '业务须知',
                isActive: true,
                visible: true,
                key: 'serverInfo'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: true,
                key: 'identifyCardPage'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'faceYitu'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'contrast'
            },
            {
                name: '信息确认',
                isActive: false,
                visible: true,
                key: 'confirmInfo'
            },
            {
                name: '输入原因',
                isActive: false,
                visible: true,
                key: 'reason'
            },
            {
                name: '上传资料',
                isActive: false,
                visible: true,
                key: 'upload'
            },
            {
                name: '邮寄信息',
                isActive: false,
                visible: true,
                key: 'inputAddress'
            },
            {
                name: '手机校验',
                isActive: false,
                visible: true,
                key: 'checkPhone'
            },
            {
                name: '手机校验',
                isActive: false,
                visible: false,
                key: 'completeProcess'
            },
            {
                name: '完成提交',
                isActive: false,
                visible: true,
                key: 'complete'
            }
        ]);
    }
};

</script>
