<template>
    <div>
        <div class="kd-title-content-wrapper kd-page-padding wrap-info">
            <text-table :data="licenceInfo"/>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
                :but-disable="!(licenceInfo && licenceInfo.length > 0)"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import textTable from '@/components/text-table';
import { getLicenceInfo } from '@/service/traffic/driver/delayExchange';

export default {
    name: 'DelayExchangeEvidenceConfirmInfo', // 确认信息
    components: { textTable },
    computed: {
        ...mapState('delayExchange', {
            licenceInfo: 'licenceInfo'
        })
    },
    mounted () {
        this.initData();
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('delayExchange', ['setLicenceInfo']),
        nextStep () { // 下一步
            this.changeStep('down');
        },
        preStep () { // 上一步
            this.changeStep('up');
        },
        async initData () {
            const res = await getLicenceInfo({});
            if (res && res.code === '0') {
                this.setLicenceInfo(res.result);
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }

    }
};
</script>

