<template>
    <div>
        <div class="kd-page-title">
            {{ title }}
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <kd-full-input
                class="reason"
                :watch-data="getReason"
                :default-content="reasonSaved || ''"
                :placeholder="title"
            />
        </div>

        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
                :but-disable="!reason"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { saveReason } from '@/service/traffic/driver/delayExchange';
import { keyboardHide } from '@/service/common';

export default {
    name: 'DelayExchangeEvidenceReason',
    computed: {
        ...mapState('delayExchange', {
            reasonSaved: 'reasonSaved'
        })
    },
    ...mapState('common', {
        businessTypeCode: 'businessTypeCode'
    }),
    ...mapState('permanent', {
        BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
    }),
    data () {
        return {
            reason: '',
            title: '请输入延期原因'
        };
    },
    mounted () {
        if (this.reasonSaved) {
            this.reason = this.reasonSaved;
        }
        if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.changeInto) {
            this.title = '请输入转入原因';
        }
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('delayExchange', ['setReason']),
        getReason (data) {
            this.reason = data;
        },
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            const res = await saveReason({
                delayCardReason: this.reason
            });
            if (res && res.code === '0') {
                this.setReason(this.reason);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        }

    },
    beforeDestroy () {
        keyboardHide();
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;

        .reason {
            margin-top: 10%;
            width: 70%;
            color: #8E9CB9;
        }
    }
</style>
