<template>
    <div>
        <div class="kd-page-title">
            手机校验
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <verify-code
                @getVerifyCode="getVerifyCode"
                ref="verifyCode"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
            />
        </div>
    </div>
</template>

<script>
import verifyCode from '@/components/verify-code';
import { mapState, mapMutations } from 'vuex';
import { getPhoneCode, getPhoneVerify } from '@/service/common';

export default {
    name: 'DelayExchangeEvidenceCheckPhone', // 手机校验
    components: { verifyCode },
    computed: {
        ...mapState('common', {
            businessTypeCode: 'businessTypeCode'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        async getVerifyCode (phoneNumber) {
            const res = await getPhoneCode({
                phone: phoneNumber,
                serviceType: this.businessTypeCode
            });
            if (!res || res.code !== '0') {
                this.changeTipPage({ message: res.message });
            }
        },
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            if (this.$refs.verifyCode.submission()) {
                const res = await getPhoneVerify({
                    phone: this.$refs.verifyCode.item.phoneNumber,
                    code: this.$refs.verifyCode.item.verifyCode,
                    serviceType: this.businessTypeCode
                });
                if (res && res.code === '0') {
                    this.changeStep('down');
                } else if (res) {
                    this.changeTipPage({
                        message: res.message,
                        enableBack: true
                    });
                }
            }
        }

    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;
        align-items: center;
    }
</style>
