import request from '@/utils/request';

/**
 * 交管业务 驾驶证延期换证 获取驾驶证信息
 * @param params
 * @returns {*}
 */
export const getLicenceInfo = (params) => {
    return request.ajax('/ssm/delaycard/getlicenceinfo', 'post', params);
};

